import { Modal, Input } from 'antd';
import TextArea from 'antd/es/input/TextArea';
import { useCallback, useEffect, useMemo, useState } from 'react';
import axios from 'axios';
import styles from './form.module.css';
import CustomInput from '../input';

export default function Form({ isModalOpen, setIsModalOpen }) {
  const [values, setValues] = useState({
    name: '',
    contact: '',
    email: '',
    additionalData: '',
  });
  const [errors, setErrors] = useState({
    name: '',
    contact: '',
    email: '',
    additionalData: '',
  });

  console.log('values', values);
  useEffect(() => {
    if (isModalOpen) {
      setValues({
        name: '',
        contact: '',
        email: '',
        additionalData: '',
      });
      setErrors({
        name: '',
        contact: '',
        email: '',
        additionalData: '',
      });
    }
  }, [isModalOpen]);

  const handleOk = useCallback(() => {
    //      ""
    const newErrors = {
      name: values.name ? '' : '  !',
      contact: values.contact ? '' : '   !',
      email: values.email ? '' : '  email!',
      additionalData: '',
    };
    setErrors(newErrors);

    //       
    if (Object.values(newErrors).some((error) => error)) {
      return; //    ,   
    }

    //  POST 
    const endpoint = process.env.REACT_APP_API_ENDPOINT; //       
    const formData = { ...values };

    console.log('endpoint', endpoint, 'formData', formData);
    axios
      .post(endpoint, formData)
      .then((response) => {
        //   
        setIsModalOpen(false);
      })
      .catch((error) => {
        //  
        setIsModalOpen(false);
        console.error(error);
      });
  }, [setIsModalOpen, values.name, values.contact, values.email]);

  const handleCancel = useCallback(() => {
    setIsModalOpen(false);
  }, [setIsModalOpen]);

  const handleInputChange = useCallback(
    (event) => {
      // -    
      const { name, value } = event.target;

      const getError = (name) => {
        switch (name) {
          case 'name':
            return '  !';
          case 'contact':
            return '   !';
          case 'email':
            return '  email!';
          default:
            return '';
        }
      };

      setErrors((prevErrors) => ({
        ...prevErrors,
        [name]: value ? '' : getError(name),
      }));

      setValues((prevValues) => ({
        ...prevValues,
        [name]: value,
      }));
    },
    [errors.name, errors.email, errors.contact]
  );

  const nameError = useMemo(() => errors.name, [errors.name]);
  const contactError = useMemo(() => errors.contact, [errors.contact]);
  const emailError = useMemo(() => errors.email, [errors.email]);

  return (
    <Modal
      position="center"
      title="  "
      open={isModalOpen}
      onOk={handleOk}
      onCancel={handleCancel}
      okText=""
      cancelText=""
      className={styles.fontClass}
      okButtonProps={{ className: styles.okButton }}
      cancelButtonProps={{ className: styles.fontClass }}
    >
      <div className={styles.formContent}>
        <CustomInput
          className={styles.fontClass}
          value={values.name}
          error={nameError}
          placeholder=" "
          name="name"
          onChange={handleInputChange}
          style={{ borderColor: values.name ? 'red' : undefined }}
        />

        <CustomInput
          className={styles.fontClass}
          value={values.contact}
          error={contactError}
          placeholder="  "
          name="contact"
          onChange={handleInputChange}
          style={{ borderColor: values.contact ? 'red' : undefined }}
        />

        <CustomInput
          className={styles.fontClass}
          value={values.email}
          error={emailError}
          placeholder=""
          name="email"
          onChange={handleInputChange}
          style={{ borderColor: values.email ? 'red' : undefined }}
        />

        <TextArea
          className={styles.fontClass}
          value={values.additionalData}
          placeholder=" "
          name="additionalData"
          onChange={handleInputChange}
        />
      </div>
    </Modal>
  );
}
